div.home-container {
    background-image: url(/static/media/bg.b59a923e.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100vh;
    /* position: absolute;  */
}
a.home-link {
    margin-left: 80px !important;
}
@media only screen and (max-width: 980px) {
    .home-container{
        background-image: none !important;
        background: linear-gradient(to bottom, #1b1418, #1d171a, #1e1a1d, #201c1f, #221f21, #262426, #2b292a, #2f2e2f, #373738, #404040, #494949, #525252) !important;        
    }
    a.home-link {
        margin-left: 0px !important;
    }    
}

.home-name{text-align:left;font-family:Futura,sans-serif;font-weight:900;font-size:7em;padding:80px;padding-top:120px !important}@-webkit-keyframes ani{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);text-shadow:0em 0em 0 #3e4355;color:#8591c4}30%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);text-shadow:0em 0em 0 #3e4355;color:#8591c4}70%{-webkit-transform:translate3d(0.08em, -0.08em, 0);transform:translate3d(0.08em, -0.08em, 0);text-shadow:-0.08em .08em #3e4355;color:#8591c4}100%{-webkit-transform:translate3d(0.08em, -0.08em, 0);transform:translate3d(0.08em, -0.08em, 0);text-shadow:-0.08em .08em #3e4355;color:#8591c4}}@keyframes ani{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);text-shadow:0em 0em 0 #3e4355;color:#8591c4}30%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0);text-shadow:0em 0em 0 #3e4355;color:#8591c4}70%{-webkit-transform:translate3d(0.08em, -0.08em, 0);transform:translate3d(0.08em, -0.08em, 0);text-shadow:-0.08em .08em #3e4355;color:#8591c4}100%{-webkit-transform:translate3d(0.08em, -0.08em, 0);transform:translate3d(0.08em, -0.08em, 0);text-shadow:-0.08em .08em #3e4355;color:#8591c4}}.home-name span{position:relative;display:inline-block;-webkit-animation:ani 1s infinite alternate cubic-bezier(0.86, 0, 0.07, 1);animation:ani 1s infinite alternate cubic-bezier(0.86, 0, 0.07, 1)}.home-name span:nth-last-child(1n){-webkit-animation-delay:-.1666666667s;animation-delay:-.1666666667s}.home-name span:nth-last-child(2n){-webkit-animation-delay:-.3333333333s;animation-delay:-.3333333333s}.home-name span:nth-last-child(3n){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@media only screen and (max-width: 980px){.home-name{font-size:3.5em;padding:10px;text-align:center}}
div.about-container {
    background: black !important;
    width: 100%;   
    color: white;
    font-weight: 200;
    text-align: center !important;
}
.about-desc {
    padding-left: 40px;
    padding-right: 40px;
    /* margin-bottom: 150px; */
}

div.project-container {
    background: black !important;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    color: white ;  
    font-weight: 100 ;     
}
img.project-img{
    width: 500px;
    height: 350px;
}
.project-title {
    text-align: left;    
    font-size: 2em;
    margin-top: 60px;
    font-weight: 400 ;     
}
.project-desc {
    text-align: left;        
    font-size: 1.1em;
    font-weight: 100;     
    margin-top: 20px;
    padding-right: 10px;
    margin-bottom: 20px;
}
.project-stack {
    text-align: left !important;        
    font-size: 0.8em;
    margin-right: 20px;
    padding: 6px;
    border: 1px solid grey;
}
.project-link {
    margin-right: 20px;
}
@media only screen and (max-width: 956px) {
    img.project-img{
        width: 100vw;
        height: 200px;
    }  
    .project-title{
        padding-left: 10px;   
    }  
    .project-desc {
        padding-left: 10px; 
    }
    .project-stack {
        margin-left: 10px !important; 
    }     
    .project-link {
        margin-left: 10px;
    }     
}
.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

